import React from 'react'
import Background from '../components/Background'
import Logo from '../components/Logo'
import Button from '../components/Button'
import { ResponseType } from "expo-auth-session";
import * as Facebook from "expo-auth-session/providers/facebook";
import * as WebBrowser from "expo-web-browser";
import { Image, StyleSheet, Text, View } from "react-native";
import { useNavigation } from '@react-navigation/native';
// import * as Passport from 'passport-snapchat';

WebBrowser.maybeCompleteAuthSession();
const FB_APP_ID = "603428947262435";

export default function StartScreen(props) {

  const navigation = useNavigation();
  const [user, setUser] = React.useState(null);
  const [request, response, promptAsync] = Facebook.useAuthRequest({
    clientId: FB_APP_ID,
    responseType: ResponseType.Token,
  });

  // console.log(Passport)
  // Passport.use(new SnapchatStrategy({
  //   clientID: '0ae09c38-ce4f-4396-858b-d10ed0e0f364',
  //   clientSecret: 'JrExFlYiTlETm3kbyw5dvPFherjNpG1OfrLPz1zimgA',
  //   callbackURL: "http://localhost:3000/auth/snapchat/callback"
  // },
  // function(accessToken, refreshToken, profile, cb) {
  //   User.findOrCreate({ snapchatId: profile.id }, function (err, user) {
  //     return cb(err, user);
  //   });
  // }
  // ));

  if (request) {
    console.log(
      "You need to add this url to your authorized redirect urls on your Facebook app: " +
        request.redirectUri
    );
  }

  React.useEffect(() => {
    if (response && response.type === "success" && response.authentication) {
      (async () => {
        const userInfoResponse = await fetch(
          `https://graph.facebook.com/me?access_token=${response.authentication.accessToken}&fields=id,name,picture.type(large)`
        );
        const userInfo = await userInfoResponse.json();
        setUser(userInfo);
      })();
    }
  }, [response]);

  React.useEffect(() => {
    if (user) {
      props.route.params.login(user)
    }
  }, [user]);

  const handlePressAsync = async () => {
    const result = await promptAsync();
    if (result.type !== "success") {
      alert("Uh oh, something went wrong");
      return;
    }
  };

  const styles = StyleSheet.create({
    facebook: {
      backgroundColor: '#4267B2',
    },
    snapchat: {
      backgroundColor: '#FFFC00',
    },
    white: {
      color: 'white',
    },
    black: {
      color: 'black',
    },
    login: {
      marginTop: 100,
      alignItems: 'center',
    },
    logo: {
      marginTop: 40,
      alignItems: 'center',
      justifyContent: 'center',
    }
  })

  return (
    <Background>
      <View style={styles.logo}>
        <Logo />
      </View>

      <View style={styles.login}>
        <Button
          mode="contained"
          onPress={() => navigation.navigate("LoginScreen")}
        >
          Login
        </Button>

        <Button
          disabled={!request}
          mode="outlined"
          onPress={handlePressAsync}
          style={styles.snapchat}
        >
          <Text style={styles.black}>Snapchat Login</Text>
        </Button>

        <Button
          disabled={!request}
          mode="outlined"
          onPress={handlePressAsync}
          style={styles.facebook}
        >
          <Text style={styles.white}>Facebook Login</Text>
        </Button>

      </View>

    </Background>
  )
}
